import { useState } from "react"
import { isCompanySuggestion } from "@rp/react/tanstack-api/suggestion"
import { RPTooltip } from "@rp/react/web/common/base-components"
import "@rp/react/web/mui/core"
import { Box, Checkbox, Typography } from "@rp/react/web/mui/core"
import { Props } from "./WatchlistSuggestionItem"

const WatchlistSuggestionItemMobile = ({
  suggestion,
  selected,
  included,
  onChange,
  disabled,
  hasFilter,
}: Props) => {
  const { key, name, description, typeDesc } = suggestion
  const [showDescription, setShowDescription] = useState(false)

  return (
    <Box
      role="button"
      aria-label={name}
      onClick={() => {
        !included && onChange(key, !selected)
      }}
      sx={{
        display: "flex",
        minWidth: 0,
        alignItems: description ? "none" : "center",
        "& > div": { borderBottom: "0px solid", py: 1.5, px: 0.5 },
      }}
    >
      {/* CHECKBOX */}
      <RPTooltip
        title="This entity is already in your watchlist"
        disableFocusListener={!included}
        disableHoverListener={!included}
      >
        <Box>
          <Checkbox
            disabled={disabled || included}
            name={name}
            checked={selected}
            onClick={ev => {
              ev.stopPropagation()
            }}
            onChange={(_, selected) => {
              onChange(key, selected)
            }}
          />
        </Box>
      </RPTooltip>

      {/* CONTENT */}
      <Box sx={{ flex: 1, minWidth: 0 }}>
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              flex: 1,
              minWidth: 0,
            }}
          >
            {/* NAME */}
            <Typography
              variant="body2"
              sx={{
                overflow: "hidden",
                whiteSpace: "nowrap",
                textOverflow: "ellipsis",
              }}
            >
              {name}
            </Typography>
            {/* TICKER*/}
            {isCompanySuggestion(suggestion) ? (
              <Typography
                id="flex-child"
                color="blue.200"
                variant="caption"
                sx={{ mx: 0.5, whiteSpace: "nowrap" }}
              >
                {suggestion.ticker}
              </Typography>
            ) : null}
          </Box>
          {/* Entity Type*/}

          {!hasFilter && (
            <Typography
              fontSize={11}
              color="text.disabled"
              pr={1}
              component="div"
              sx={{ float: "right" }}
            >
              {typeDesc}
            </Typography>
          )}
        </Box>
        {/* DESCRIPTION*/}
        {description ? (
          <Typography
            color="text.disabled"
            variant="body2"
            onClick={ev => {
              setShowDescription(!showDescription)
              ev.stopPropagation()
            }}
            sx={{
              pr: 1,
              textOverflow: "ellipsis",
              overflowY: "hidden",
              display: "-webkit-box",
              overflowWrap: "break-word",
              WebkitBoxOrient: "vertical",
              WebkitLineClamp: showDescription ? 30 : 2, // 30 lines just to not leave it as infinite
            }}
          >
            {description}
          </Typography>
        ) : null}
      </Box>
    </Box>
  )
}

export { WatchlistSuggestionItemMobile }
